@use "global" as *;
@use "sass:math";


.p-media {
  width: 100%;
  @include mq() {
    display: flex;
    align-items: center;
  }
}

.p-media__text {
  font-weight: $bold;
  letter-spacing: 0.05em;
  line-height: math.div( 30, 16);
  @include mq() {
    width: 100% * math.div( 440, 1000);
  }

}

.p-media__image {
  position: relative;
  width: 100%;
  margin-top: rem(27);
  @include mq() {
    width: 100% * math.div( 440, 1000);
    margin-top: 0;
    margin-left: 100% * math.div( 120, 1000)
  }
}

.p-media__image::before {
  content:"";
  display: block;
  padding-top: 100% * math.div( 150, 345 );
  @include mq() {
    padding-top: 100% * math.div( 240, 440 );
  }
}

.p-media__image img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  object-fit: cover;
  object-position: bottom;
  height: 100%;
  width: 100%;
}

